<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <div class="mb-4">
          <img alt="FuryGames" src="/static/images/FuryGames-Logo.png" />
        </div>
        <v-carousel id="carousel">
          <v-carousel-item v-for="(item,i) in items" :src="item.src" :key="i"></v-carousel-item>
        </v-carousel>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<style lang="scss" scoped>
#carousel {
  max-width: 950px;
}
</style>

<script>
  export default {
    data () {
      return {
        items: [
          {
            src: '/static/images/carousel/Carousel1.png'
          },
          {
            src: '/static/images/carousel/Carousel2.png'
          },
          {
            src: '/static/images/carousel/Carousel3.png'
          }
        ]
      }
    }
  }
</script>


